import localforage from "localforage";
import uniqid from "uniqid";
import RandomColor from "../tools/RandomColor";
import Vue from "vue";

const db = localforage.createInstance({
  name: "categorys"
});

const state = {
  categorys: {}
};

const getters = {
  categorys(state) {
    return state.categorys;
  }
};

//TODO mutations
const mutations = {
  //
  initCategorys(state, categorys) {
    state.categorys = categorys;
  },
  //
  createCategory(state, data) {
    state.categorys[data.id] = data;
    Vue.set(state, "categorys", { ...state.categorys });
  },
  //
  updatedCategory(state, data) {
    state.categorys[data.id] = data;
    Vue.set(state, "categorys", { ...state.categorys });
  },
  //
  deleteCategory(state, id) {
    delete state.categorys[id];
    Vue.set(state, "categorys", { ...state.categorys });
  },
  //start
  toggleStar(state, id) {
    state.categorys[id].star = !state.categorys[id].star;
    Vue.set(state, "categorys", { ...state.categorys });
  },
  //
  toggleArchive(state, id) {
    state.categorys[id].active = !state.categorys[id].active;
    Vue.set(state, "categorys", { ...state.categorys });
  },
  //
  setShortcut(state, data) {
    state.categorys[data.id].shortcut = data.shortcut;
    Vue.set(state, "categorys", { ...state.categorys });
  }
};

//DONE: 
const actions = {
  setCategorysByFile(n, categorys) {
    //1. 

    //2. 
    for (let id in categorys) {
      db.setItem(id, categorys[id]);
    }

    // commit("initCategorys", categorys);
  },
  // 
  async initCategorys({ commit }) {
    const categorys = {};
    await db.iterate(function(value, key) {
      categorys[key] = value;
    });
    commit("initCategorys", categorys);
  },
  //
  async createCategory({ commit }, data) {
    const id = uniqid("category-");
    //
    if (data.color === undefined) data.color = RandomColor();
    if (data.description === undefined || data.description.trim() === "")
      data.description = "...";
    const createCategory = {
      id,
      ...data,
      shortcut: [],
      star: false,
      active: true
    };
    try {
      const item = await db.setItem(id, createCategory);
      commit("createCategory", item);
    } catch (err) {
      console.log(err);
    }
  },

  //
  async updatedCategory({ commit }, data) {
    //
    if (data.color === undefined) data.color = RandomColor();
    if (data.description === undefined || data.description.trim() === "")
      data.description = "...";

    const item = await db.getItem(data.id);
    item.name = data.name;
    item.color = data.color;
    item.description = data.description;

    try {
      const newItem = await db.setItem(data.id, item);
      commit("updatedCategory", newItem);
    } catch (err) {
      console.log(err);
    }
  },

  //
  async deleteCategory({ commit }, id) {
    try {
      await db.removeItem(id);
      commit("deleteCategory", id);
    } catch (err) {
      console.log(err);
    }
  },

  //
  async toggleStar({ commit }, id) {
    try {
      const item = await db.getItem(id);
      item.star = !item.star;
      await db.setItem(id, item);
      commit("toggleStar", id);
      // console.log(item.star)
    } catch (err) {
      console.log(err);
    }
  },

  //active
  async toggleArchive({ commit }, id) {
    try {
      const item = await db.getItem(id);
      item.active = !item.active;
      await db.setItem(id, item);
      commit("toggleArchive", id);
    } catch (err) {
      console.log(err);
    }
  },

  //
  async setShortcut({ commit }, data) {
    try {
      const item = await db.getItem(data.id);
      item.shortcut = data.shortcut;
      await db.setItem(data.id, item);
      commit("setShortcut", data);
    } catch (err) {
      console.log(err);
    }
  }
  //TODO: 
};

export default {
  state,
  getters,
  mutations,
  actions
};
